'use strict';
var slideShow = (function () {
    return function (selector, config) {
        var
            _slider = document.querySelector(selector), // –æ—Å–Ω–æ–≤–Ω—ã–π —ç–ª–µ–º–µ–Ω—Ç –±–ª–æ–∫–∞
            _sliderContainer = _slider.querySelector('.slider__items'), // –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è .slider-item
            _sliderItems = _slider.querySelectorAll('.slider__item'), // –∫–æ–ª–ª–µ–∫—Ü–∏—è .slider-item
            _sliderControls = _slider.querySelectorAll('.slider__control'), // —ç–ª–µ–º–µ–Ω—Ç—ã —É–ø—Ä–∞–≤–ª–µ–Ω–∏—è
            _currentPosition = 0, // –ø–æ–∑–∏—Ü–∏—è –ª–µ–≤–æ–≥–æ –∞–∫—Ç–∏–≤–Ω–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞
            _transformValue = 0, // –∑–Ω–∞—á–µ–Ω–∏–µ —Ç—Ä–∞–Ω—Ñ—Å–æ—Ñ—Ä–º–∞—Ü–∏–∏ .slider_wrapper
            _transformStep = 100, // –≤–µ–ª–∏—á–∏–Ω–∞ —à–∞–≥–∞ (–¥–ª—è —Ç—Ä–∞–Ω—Å—Ñ–æ—Ä–º–∞—Ü–∏–∏)
            _itemsArray = [], // –º–∞—Å—Å–∏–≤ —ç–ª–µ–º–µ–Ω—Ç–æ–≤
            _timerId,
            _indicatorItems,
            _indicatorIndex = 0,
            _indicatorIndexMax = _sliderItems.length - 1,
            _stepTouch = 50,
            _config = {
                isAutoplay: false, // –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∞—è —Å–º–µ–Ω–∞ —Å–ª–∞–π–¥–æ–≤
                directionAutoplay: 'next', // –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ —Å–º–µ–Ω—ã —Å–ª–∞–π–¥–æ–≤
                delayAutoplay: 5000, // –∏–Ω—Ç–µ—Ä–≤–∞–ª –º–µ–∂–¥—É –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–æ–π —Å–º–µ–Ω–æ–π —Å–ª–∞–π–¥–æ–≤
                isPauseOnHover: false // —É—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞—Ç—å –ª–∏ –ø–∞—É–∑—É –ø—Ä–∏ –ø–æ–¥–Ω–µ—Å–µ–Ω–∏–∏ –∫—É—Ä—Å–æ—Ä–∞ –∫ —Å–ª–∞–π–¥–µ—Ä—É
            };

        // –Ω–∞—Å—Ç—Ä–æ–π–∫–∞ –∫–æ–Ω—Ñ–∏–≥—É—Ä–∞—Ü–∏–∏ —Å–ª–∞–π–¥–µ—Ä–∞ –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç –ø–æ–ª—É—á–µ–Ω–Ω—ã—Ö –∫–ª—é—á–µ–π
        for (var key in config) {
            if (key in _config) {
                _config[key] = config[key];
            }
        }

        // –Ω–∞–ø–æ–ª–Ω–µ–Ω–∏–µ –º–∞—Å—Å–∏–≤–∞ _itemsArray
        for (var i = 0, length = _sliderItems.length; i < length; i++) {
            _itemsArray.push({
                item: _sliderItems[i],
                position: i,
                transform: 0
            });
        }

        // –ø–µ—Ä–µ–º–µ–Ω–Ω–∞—è position —Å–æ–¥–µ—Ä–∂–∏—Ç –º–µ—Ç–æ–¥—ã —Å –ø–æ–º–æ—â—å—é –∫–æ—Ç–æ—Ä–æ–π –º–æ–∂–Ω–æ –ø–æ–ª—É—á–∏—Ç—å –º–∏–Ω–∏–º–∞–ª—å–Ω—ã–π –∏ –º–∞–∫—Å–∏–º–∞–ª—å–Ω—ã–π –∏–Ω–¥–µ–∫—Å —ç–ª–µ–º–µ–Ω—Ç–∞, –∞ —Ç–∞–∫–∂–µ —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â–µ–º—É —ç—Ç–æ–º—É –∏–Ω–¥–µ–∫—Å—É –ø–æ–∑–∏—Ü–∏—é
        var position = {
            getItemIndex: function (mode) {
                var index = 0;
                for (var i = 0, length = _itemsArray.length; i < length; i++) {
                    if ((_itemsArray[i].position < _itemsArray[index].position && mode === 'min') || (_itemsArray[i].position > _itemsArray[index].position && mode === 'max')) {
                        index = i;
                    }
                }
                return index;
            },
            getItemPosition: function (mode) {
                return _itemsArray[position.getItemIndex(mode)].position;
            }
        };

        // —Ñ—É–Ω–∫—Ü–∏—è, –≤—ã–ø–æ–ª–Ω—è—é—â–∞—è —Å–º–µ–Ω—É —Å–ª–∞–π–¥–∞ –≤ —É–∫–∞–∑–∞–Ω–Ω–æ–º –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–∏
        var _move = function (direction) {
            var nextItem, currentIndicator = _indicatorIndex;;
            if (direction === 'next') {
                _currentPosition++;
                if (_currentPosition > position.getItemPosition('max')) {
                    nextItem = position.getItemIndex('min');
                    _itemsArray[nextItem].position = position.getItemPosition('max') + 1;
                    _itemsArray[nextItem].transform += _itemsArray.length * 100;
                    _itemsArray[nextItem].item.style.transform = 'translateX(' + _itemsArray[nextItem].transform + '%)';
                }
                _transformValue -= _transformStep;
                _indicatorIndex = _indicatorIndex + 1;
                if (_indicatorIndex > _indicatorIndexMax) {
                    _indicatorIndex = 0;
                }
            } else {
                _currentPosition--;
                if (_currentPosition < position.getItemPosition('min')) {
                    nextItem = position.getItemIndex('max');
                    _itemsArray[nextItem].position = position.getItemPosition('min') - 1;
                    _itemsArray[nextItem].transform -= _itemsArray.length * 100;
                    _itemsArray[nextItem].item.style.transform = 'translateX(' + _itemsArray[nextItem].transform + '%)';
                }
                _transformValue += _transformStep;
                _indicatorIndex = _indicatorIndex - 1;
                if (_indicatorIndex < 0) {
                    _indicatorIndex = _indicatorIndexMax;
                }
            }
            _sliderContainer.style.transform = 'translateX(' + _transformValue + '%)';
            _indicatorItems[currentIndicator].classList.remove('active');
            _indicatorItems[_indicatorIndex].classList.add('active');
        };

        // —Ñ—É–Ω–∫—Ü–∏—è, –æ—Å—É—â–µ—Å—Ç–≤–ª—è—é—â–∞—è –ø–µ—Ä–µ—Ö–æ–¥ –∫ —Å–ª–∞–π–¥—É –ø–æ –µ–≥–æ –ø–æ—Ä—è–¥–∫–æ–≤–æ–º—É –Ω–æ–º–µ—Ä—É
        var _moveTo = function (index) {
            var i = 0,
                direction = (index > _indicatorIndex) ? 'next' : 'prev';
            while (index !== _indicatorIndex && i <= _indicatorIndexMax) {
                _move(direction);
                i++;
            }
        };

        // —Ñ—É–Ω–∫—Ü–∏—è –¥–ª—è –∑–∞–ø—É—Å–∫–∞ –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–æ–π —Å–º–µ–Ω—ã —Å–ª–∞–π–¥–æ–≤ —á–µ—Ä–µ–∑ –ø—Ä–æ–º–µ–∂—É—Ç–∫–∏ –≤—Ä–µ–º–µ–Ω–∏
        var _startAutoplay = function () {
            if (!_config.isAutoplay) {
                return;
            }
            _stopAutoplay();
            _timerId = setInterval(function () {
                _move(_config.directionAutoplay);
            }, _config.delayAutoplay);
        };

        // —Ñ—É–Ω–∫—Ü–∏—è, –æ—Ç–∫–ª—é—á–∞—é—â–∞—è –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫—É—é —Å–º–µ–Ω—É —Å–ª–∞–π–¥–æ–≤
        var _stopAutoplay = function () {
            clearInterval(_timerId);
        };

        // —Ñ—É–Ω–∫—Ü–∏—è, –¥–æ–±–∞–≤–ª—è—é—â–∞—è –∏–Ω–¥–∏–∫–∞—Ç–æ—Ä—ã –∫ —Å–ª–∞–π–¥–µ—Ä—É
        var _addIndicators = function () {
            var indicatorsContainer = document.createElement('ol');
            indicatorsContainer.classList.add('slider__indicators');
            for (var i = 0, length = _sliderItems.length; i < length; i++) {
                var sliderIndicatorsItem = document.createElement('li');
                if (i === 0) {
                    sliderIndicatorsItem.classList.add('active');
                }
                sliderIndicatorsItem.setAttribute("data-slide-to", i);
                indicatorsContainer.appendChild(sliderIndicatorsItem);
            }
            _slider.appendChild(indicatorsContainer);
            _indicatorItems = _slider.querySelectorAll('.slider__indicators > li')
        };

        var _isTouchDevice = function () {
            return !!('ontouchstart' in window || navigator.maxTouchPoints);
        };

        // —Ñ—É–Ω–∫—Ü–∏—è, –æ—Å—É—â–µ—Å—Ç–≤–ª—è—é—â–∞—è —É—Å—Ç–∞–Ω–æ–≤–∫—É –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–æ–≤ –¥–ª—è —Å–æ–±—ã—Ç–∏–π 
        var _setUpListeners = function () {
            var _startX = 0;
            if (_isTouchDevice()) {
                _slider.addEventListener('touchstart', function (e) {
                    _startX = e.changedTouches[0].clientX;
                    _startAutoplay();
                });
                _slider.addEventListener('touchend', function (e) {
                    var
                        _endX = e.changedTouches[0].clientX,
                        _deltaX = _endX - _startX;
                    if (_deltaX > _stepTouch) {
                        _move('prev');
                    } else if (_deltaX < -_stepTouch) {
                        _move('next');
                    }
                    _startAutoplay();
                });
            } else {
                for (var i = 0, length = _sliderControls.length; i < length; i++) {
                    _sliderControls[i].classList.add('slider__control_show');
                }
            }
            _slider.addEventListener('click', function (e) {
                if (e.target.classList.contains('slider__control')) {
                    e.preventDefault();
                    _move(e.target.classList.contains('slider__control_next') ? 'next' : 'prev');
                    _startAutoplay();
                } else if (e.target.getAttribute('data-slide-to')) {
                    e.preventDefault();
                    _moveTo(parseInt(e.target.getAttribute('data-slide-to')));
                    _startAutoplay();
                }
            });
            document.addEventListener('visibilitychange', function () {
                if (document.visibilityState === "hidden") {
                    _stopAutoplay();
                } else {
                    _startAutoplay();
                }
            }, false);
            if (_config.isPauseOnHover && _config.isAutoplay) {
                _slider.addEventListener('mouseenter', function () {
                    _stopAutoplay();
                });
                _slider.addEventListener('mouseleave', function () {
                    _startAutoplay();
                });
            }
        };

        // –¥–æ–±–∞–≤–ª—è–µ–º –∏–Ω–¥–∏–∫–∞—Ç–æ—Ä—ã –∫ —Å–ª–∞–π–¥–µ—Ä—É
        _addIndicators();
        // —É—Å—Ç–∞–Ω–æ–≤–ª–∏–≤–∞–µ–º –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–∏ –¥–ª—è —Å–æ–±—ã—Ç–∏–π
        _setUpListeners();
        // –∑–∞–ø—É—Å–∫–∞–µ–º –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫—É—é —Å–º–µ–Ω—É —Å–ª–∞–π–¥–æ–≤, –µ—Å–ª–∏ —É—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â–∏–π –∫–ª—é—á
        _startAutoplay();

        return {
            // –º–µ—Ç–æ–¥ —Å–ª–∞–π–¥–µ—Ä–∞ –¥–ª—è –ø–µ—Ä–µ—Ö–æ–¥–∞ –∫ —Å–ª–µ–¥—É—é—â–µ–º—É —Å–ª–∞–π–¥—É
            next: function () {
                _move('next');
            },
            // –º–µ—Ç–æ–¥ —Å–ª–∞–π–¥–µ—Ä–∞ –¥–ª—è –ø–µ—Ä–µ—Ö–æ–¥–∞ –∫ –ø—Ä–µ–¥—ã–¥—É—â–µ–º—É —Å–ª–∞–π–¥—É          
            left: function () {
                _move('prev');
            },
            // –º–µ—Ç–æ–¥ –æ—Ç–∫–ª—é—á–∞—é—â–∏–π –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫—É—é —Å–º–µ–Ω—É —Å–ª–∞–π–¥–æ–≤
            stop: function () {
                _config.isAutoplay = false;
                _stopAutoplay();
            },
            // –º–µ—Ç–æ–¥ –∑–∞–ø—É—Å–∫–∞—é—â–∏–π –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫—É—é —Å–º–µ–Ω—É —Å–ª–∞–π–¥–æ–≤
            cycle: function () {
                _config.isAutoplay = false;
                _startAutoplay();
            }
        }
    }
}());

slideShow('.slider', {
    isAutoplay: true
});